<template>
	<div class="container" @click="handleGallaryClick">
		 <div class="wrapper">
		 	 <swiper  :options="swiperOption" >
                   <swiper-slide v-for="(item,index) in imgs">
                      <img class="gallary-img" :src="item">
                   </swiper-slide>
                  
                   <div class="swiper-pagination"  slot="pagination"></div>
             </swiper> 
		 </div>
	</div>
</template>

<script type="text/javascript">
	export default{
	  props:{
         imgs:{
         	type:Array,
         }
		},
      data(){
      	return{
      		swiperOption:{
      			pagination:".swiper-pagination",
      			paginationType:"fraction",
      			observeParents:true,
      			observer:true,
      		}
      	}
      },
      methods:{
      	handleGallaryClick(){
      		this.$emit("close")
      	}
      }
	}
</script>

<style type="text/css" media="screen" scoped>
.container{
	display: flex;
	flex-direction: column;
	justify-content: center;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: #000;
	z-index:99;
}	
.wrapper{
    overflow: hidden;
    height: 0;
    width: 100%;
    padding-bottom: 100%;
}
.gallary-img{
	width: 100%;
}
.swiper-pagination{
	color: #fff;
}
</style>


