<template>
<div>

<div class="banner" @click="handleBannerClick">
	<img :src="bannerImg">
	    <div class="banner-info">
		  <div class="banner-title"> {{this.sightName}} </div>
		  <div class="banner-number"><span class="iconfont biao">&#xe63e;</span></div>
		</div>
	</div>
	<fade-animation>
         <common-gallary :imgs="bannerImgs" v-show="showGallary" @close="handleBannerClose"> </common-gallary>
	</fade-animation>
	
</div>

</template>
<script type="text/javascript">
 import CommonGallary from "./gallary"
 import FadeAnimation from "./fade"
 export default{
 	props:{
        sightName:String,
        bannerImg:String,
        bannerImgs:Array,
 	},
 	components:{
 		CommonGallary,
 		FadeAnimation,
 	},
 	data (){
 		return{
 			showGallary:false,
 	          }
              },
    methods:{
    	handleBannerClick(){
    		this.showGallary=true
    		console.log(this.sightName)
    	},
    	handleBannerClose(){
    		this.showGallary=false
    	}
    }          
 }
	
</script>
<style type="text/css" media="screen">
	.banner{
		position: relative;
		overflow: hidden;
		height: 0;
		padding-bottom: 55%;
	}
	.banner-img{
		width: 100%;
	}
	.banner-info{
		display: flex;
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		line-height: .6rem;
		color: #fff;
		background-image: linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,.8))
	}
	.banner-title{
		flex: 1;
		font-size: .32rem;
		padding:0  .2rem;
	}
	.biao{
		font-size: .30rem;
	}
	.banner-number{
		margin-top: .08rem;
		margin-right: .1rem;
		padding:0  .4rem;
		height: .4rem;
		line-height: .4rem;
		font-size: .30rem;
		border-radius: .2rem;
		background-color: rgba(0,0,0,.8);	}
</style>


