<template>
   <div>
      <router-link tag="div" to="/" class="header-abs" v-show="showAbs">
      	 <div class="iconfont header-abs-back">&#xe624;</div>
      </router-link>
      <div class="header-fixed" v-show="!showAbs">
      	<router-link to="/">
           <div class="iconfont header-fixed-back">
            	&#xe624;
            </div> 
      	</router-link>
      	景点详情
      </div> 

  </div>
</template>
<script type="text/javascript">
	export default{
		components:{
		
		},
		data(){
			return{
				showAbs:true,
				opacityStyle:{
					opacity:0,
				}
			}
		},
		methods:{
			handleScroll(){
				const top =document.documentElement.scrollTop
				if(top>50){
					$(".header-fixed").fadeIn(1500)
				}else{
					$(".header-fixed").fadeOut(1500);
				}
			}
		},
		activated (){
			window.addEventListener("scroll",this.handleScroll)
		},
		deactivated (){
			window.removeEventListener("scroll",this.handleScroll)
		}
	}
</script>
<style type="text/css" media="screen" scoped>
	.header-abs{
		position: absolute;
		left: .2rem;
		top: .2rem;
		width: .8rem;
		height: .8rem;
		border-radius: .4rem;
		line-height: .8rem;
		text-align: center;
		background-color: rgba(0,0,0,.8);
	}
	.header-abs-back{
		color: #fff;
		font-size: .4rem;
	}
	.header-fixed{
	    z-index:2;
		overflow: hidden;
		height: .86rem;
		line-height: .86rem;
        text-align:center;
        color:#fff;
        background-color:#00bcd4;
        font-size: .32rem;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
	}
	.header-fixed-back{
		position: absolute;
		top: 0;
		left: 0.2rem;
		color: #fff;
	}
</style>


